<template>
  <div id="app">
    <nav class="sidebar close">
      <header>
        <div class="image-text">
          <span class="image">
            <!-- <img src="./" alt=""> -->
          </span>
          <div class="text logo-text">
            <span class="name">在线产品设计</span>
          </div>
        </div>
        <i class="bx bx-chevron-right toggle"></i>
      </header>

      <div class="menu-bar">
        <div class="menu">
          <ul class="menu-links">
            <li class="nav-link">
              <i class='bx bx-bar-chart-alt-2 icon'></i>
              <span class="text nav-text">款式　</span>
              <span class="text nav-text"><label for="product-select" class="text nav-text"></label>
                <!-- 绑定 v-model 实现双向数据绑定 -->
                <select id="product-select" v-model="product">
                  <!-- 使用 v-for 指令渲染选项 -->
                  <option v-for="product in products" :key="product" :value="product">
                    {{ product }}
                  </option>
                </select></span>
            </li>

            <!-- 颜色 -->
            <li class="nav-link">
              <i class='bx icon'>
                <ColorPicker v-model="color" modebar="none"></ColorPicker>
              </i>

            </li>

            <li class="nav-link">
              <span class="text">　宽度：</span>
              <input v-model="width" class="size-input"></input>
            </li>

            <li class="nav-link">
              <span class="text">　高度：</span>
              <input v-model="height" class="size-input"></input>
            </li>

            <li class="nav-link" v-show="thickness_isVisible">
              <span class="text">　厚度：</span>
              <input v-model="thickness" class="size-input"></input>
            </li>

            <!-- LOGO -->

            <li class="nav-link">
              <span class="text">　<input type="file" @change="handleFileUpload" accept="image/*" /></span>
            </li>

            <li class="nav-link" v-show="logo_isVisible">
              <span class="text">　宽度：</span>
              <input v-model="logo_width" class="size-input"></input>
            </li>

            <li class="nav-link" v-show="logo_isVisible">
              <span class="text">　高度：</span>
              <input v-model="logo_height" class="size-input"></input>
            </li>


            <!-- 截图 -->
            <li class="nav-link" @click="screenshot()">
              <a href="#">
                <span class="text nav-text full-width-span">截图保存</span>
              </a>
            </li>
          </ul>
        </div>
        <div class="bottom-content">
          <li class="mode">
            <div class="sun-moon">
              <i class="bx bx-moon icon moon"></i>
              <i class="bx bx-sun icon sun"></i>
            </div>
            <span class="mode-text text">护眼模式</span>
            <div class="toggle-switch">
              <span class="switch"></span>
            </div>
          </li>
        </div>

      </div>
    </nav>
  </div>

  <div class="home">
    <canvas id="show" touch-action="none"></canvas>
  </div>

</template>

<script setup>
import ColorPicker from '@mcistudio/vue-colorpicker'
import '@mcistudio/vue-colorpicker/dist/style.css'
</script>


<script>
import * as BABYLON from 'babylonjs'; //Babylon
import * as BABYLON_LOADER from 'babylonjs-loaders';
import * as GUI from 'babylonjs-gui';

export default {

  data() {
    return {
      products: ['帆布袋-背心袋', '帆布袋-束口', '帆布袋-无底无侧', '帆布袋-有底无侧', '帆布袋-有底有侧', '无纺布袋-背心袋', '无纺布袋-平口袋', '无纺布袋-束口', '无纺布袋-有底无侧', '无纺布袋-有底有侧'],
      product: '帆布袋-背心袋',
      thickness_isVisible: false,
      logo_isVisible: false,
      canvas: null,
      engine: null,
      scene: null,
      width: 300,
      height: 230,
      thickness: 0,
      color: '#ff0000',
      newMaterial: null,
      bag: null,
      bag2: null,
      bag2_clone: null,
      bag3: null,
      logo_width: 200,
      logo_height: 200,
      logo_position_x: 60,
      logo_position_y: 0,
      box: null,
      w_Text: null,
      h_Text: null,
      s_Text: null,
      color_Text: null,
      logo_width_Text: null,
      logo_height_Text: null,
      logo_distance_Text: null,
      fontData: null,
      textMaterial: null,
      logoMaterial: null,
      frontLight: null
    };
  },

  async mounted() {
    const body = document.querySelector('body');
    const sidebar = body.querySelector('nav');
    const toggle = body.querySelector('.toggle');
    const searchBtn = body.querySelector('.search-box');
    const modeSwitch = body.querySelector('.toggle-switch');
    const modeText = body.querySelector('.mode-text');
    toggle.addEventListener('click', () => {
      sidebar.classList.toggle('close');
    });

    modeSwitch.addEventListener('click', () => {
      body.classList.toggle('dark');
      if (body.classList.contains('dark')) {
        modeText.innerText = "Light mode";
      } else {
        modeText.innerText = "Dark mode";
      }
    });

    //初始化
    var canvas = document.getElementById("show"); // 创建画布
    var engine = new BABYLON.Engine(canvas, true); // 创建渲染引擎
    const scene = new BABYLON.Scene(engine);
    this.canvas = canvas;
    this.engine = engine;
    this.scene = scene;

    const boxmaterial = new BABYLON.StandardMaterial("boxmaterial", this.scene);
    // 设置材质的透明度，值在 0（完全透明）到 1（完全不透明）之间
    boxmaterial.alpha = 0;
    // 创建一个盒子
    const box = BABYLON.MeshBuilder.CreateBox("box", { size: 0.2 }, this.scene);
    // 将材质应用到盒子上
    box.material = boxmaterial;
    //盒子大小和位置
    box.scaling = new BABYLON.Vector3(1, 1, 0.005);
    box.position = new BABYLON.Vector3(0, 0.11, -0.05);
    //允许拖拽盒子
    const dragBehavior = new BABYLON.PointerDragBehavior({
      dragPlaneNormal: new BABYLON.Vector3(0, 0, 1) // 设置拖动平面的法线，限制在 X 和 Y 轴上
    });
    box.addBehavior(dragBehavior);
    box.isVisible = false;
    this.box = box;
    //背景颜色
    scene.clearColor = new BABYLON.Color4(0.8, 0.9, 1, 1);

    //产品参数
    if (window.earcut) {
      var fontData = await (await fetch("./Droid Sans_Regular.json")).json();
      this.fontData = fontData;
      //文本参数颜色
      const textMaterial = new BABYLON.StandardMaterial("textMaterial", scene);
      textMaterial.diffuseColor = new BABYLON.Color3(0, 0, 0);
      this.textMaterial = textMaterial;
      //LOGO文本参数颜色
      const logoMaterial = new BABYLON.StandardMaterial("logoMaterial", scene);
      this.logoMaterial = logoMaterial;
      //宽度参数文本
      var w_Text_string = this.width.toString();
      var w_Text = BABYLON.MeshBuilder.CreateText("w_Text", w_Text_string + "mm", fontData, {
        size: 0.02,
        depth: 0.001
      });
      w_Text.material = textMaterial;
      w_Text.position = new BABYLON.Vector3(0, -0.1, 0)
      this.w_Text = w_Text;
      //高度参数文本
      var h_Text_string = this.height.toString();
      var h_Text = BABYLON.MeshBuilder.CreateText("w_Text", h_Text_string + "mm", fontData, {
        size: 0.02,
        depth: 0.001
      });
      h_Text.material = textMaterial;
      h_Text.position = new BABYLON.Vector3(-0.3, 0.13, 0)
      this.h_Text = h_Text;
      //厚度参数文本
      var s_Text_string = this.thickness.toString();
      var s_Text = BABYLON.MeshBuilder.CreateText("w_Text", s_Text_string + "mm", fontData, {
        size: 0.02,
        depth: 0.001
      });
      s_Text.material = textMaterial;
      s_Text.position = new BABYLON.Vector3(0.5, -0.1, 0)
      this.s_Text = s_Text;
      //LOGO宽度参数文本
      var logo_width_Text_string = this.logo_width.toString();
      var logo_width_Text = BABYLON.MeshBuilder.CreateText("logo_width_Text", logo_width_Text_string + "mm", this.fontData, {
        size: 0.02,
        depth: 0.001
      });
      logo_width_Text.material = this.logoMaterial;
      logo_width_Text.material.emissiveColor = new BABYLON.Color3(1, 0, 0);
      logo_width_Text.position = new BABYLON.Vector3(0, -0.05, 0)
      logo_width_Text.isVisible = false;
      this.logo_width_Text = logo_width_Text;
      //LOGO高度参数文本
      var logo_height_Text_string = this.logo_height.toString();
      var logo_height_Text = BABYLON.MeshBuilder.CreateText("logo_height_Text", logo_height_Text_string + "mm", this.fontData, {
        size: 0.02,
        depth: 0.001
      });
      logo_height_Text.material = this.logoMaterial;
      logo_height_Text.material.emissiveColor = new BABYLON.Color3(1, 0, 0);
      logo_height_Text.position = new BABYLON.Vector3(this.box.position.x + 0.19, this.box.position.y, -0.05)
      logo_height_Text.isVisible = false;
      this.logo_height_Text = logo_height_Text;
      //LOGO顶部距离参数文本
      var logo_distance_Text_string = (-20000 / 19 * this.box.position.y + 145.79).toFixed(0).toString();
      var logo_distance_Text = BABYLON.MeshBuilder.CreateText("logo_distance_Text", logo_distance_Text_string + "mm", this.fontData, {
        size: 0.02,
        depth: 0.001
      });
      logo_distance_Text.material = this.logoMaterial;
      logo_distance_Text.position = new BABYLON.Vector3(this.box.position.x + 0.19, this.box.position.y + 0.105, -0.05)
      logo_distance_Text.isVisible = false;
      this.logo_distance_Text = logo_distance_Text;
      //颜色参数文本
      var color_Text = BABYLON.MeshBuilder.CreateText("color_Text", "rgb: " + "0.0, 0.0, 0.0", this.fontData, {
        size: 0.02,
        depth: 0.001
      });
      color_Text.material = this.textMaterial;
      color_Text.position = new BABYLON.Vector3(0, -0.3, 0)
      this.color_Text = color_Text;
    } else {
      console.log('earcut未正确加载或不可访问');
    }
    //导入默认模型
    BABYLON.SceneLoader.ImportMeshAsync("", "./", "帆布袋-背心袋.glb").then((result) => {
      this.bag = result.meshes.find(mesh => mesh.id === "bag")
      this.w = result.meshes.find(mesh => mesh.id === "w")
      this.w.material.emissiveColor = new BABYLON.Color3(1, 0, 0);
      this.h = result.meshes.find(mesh => mesh.id === "h")
      this.h.material.emissiveColor = new BABYLON.Color3(1, 0, 0);
      if (result.meshes.find(mesh => mesh.id === "s")) {
        this.s = result.meshes.find(mesh => mesh.id === "s")
        this.s.material.emissiveColor = new BABYLON.Color3(1, 0, 0);
        this.s.position.x = -0.5
      }
      s_Text.dispose();

      //复制w
      this.w_clone = this.w.clone("w_clone");
      this.w_clone.scaling.x = 200 / 290;
      this.w_clone.position = new BABYLON.Vector3(this.box.position.x, this.box.position.y - 0.065, -0.05);
      this.w_clone.isVisible = false;
      //复制h
      this.h_clone = this.h.clone("h_clone");
      this.h_clone.scaling.y = 200 / 235;
      this.h_clone.scaling.x = -1 * this.h.scaling.x;
      this.h_clone.position = new BABYLON.Vector3(this.box.position.x + 0.09, this.box.position.y - 0.1, -0.05);
      this.h_clone.isVisible = false;
      //复制h2
      this.h_clone2 = this.h_clone.clone("h_clone2");
      this.h_clone2.scaling.y = (this.height - this.logo_height) / 250;
      this.h_clone2.position = new BABYLON.Vector3(this.h_clone.position.x, this.h_clone.position.y + 0.2, -0.05);
      this.h_clone2.isVisible = false;
    });

    //设置摄像头
    const camera = new BABYLON.ArcRotateCamera("Camera", -Math.PI / 2, Math.PI / 2.5, 10, new BABYLON.Vector3(0.1, 0.1, 0), scene);
    //camera.farPlane = 1000;
    camera.nearPlane = 0.1;
    this.camera = camera;
    camera.fov = Math.PI / 5;
    //滑动控制
    camera.attachControl(canvas, true);
    //camera.setTarget(BABYLON.Vector3.Zero());
    //半径
    camera.radius = 2;
    camera.lowerRadiusLimit = 1.3;
    camera.upperRadiusLimit = 3;
    //限制摄像头查看底部
    camera.upperBetaLimit = Math.PI / 2.2;
    camera.lowerBetaLimit = Math.PI / 2.2;
    camera.lowerAlphaLimit = -1.6;//限制向左旋转的最小角度
    camera.upperAlphaLimit = -1.6;//限制向右旋转的最大角度

    // 创建从正面照射的平行光
    const frontLight = new BABYLON.PointLight("frontLight", new BABYLON.Vector3(0, 0, -1), this.scene);
    frontLight.intensity = 0.5;
    frontLight.range = 10;
    frontLight.position.z = -1;
    frontLight.includedOnlyMeshes = [this.box];
    this.frontLight = frontLight;

    //监听LOGO拖动
    const drag = new BABYLON.PointerDragBehavior();
    this.box.addBehavior(drag);
    dragBehavior.onDragEndObservable.add(() => {
      //console.log(box.position.y);
      if (box.position.x !== 0) {
        //灯光
        this.frontLight.position.x = this.box.position.x;
        this.frontLight.position.y = this.box.position.y - 0.1;
        //线条
        this.w_clone.position = new BABYLON.Vector3(-this.box.position.x, this.box.position.y - 0.065 - (this.logo_height - 200) * 0.0005, -0.05);
        this.h_clone.position = new BABYLON.Vector3(-this.box.position.x + 0.092 - (this.logo_width - 200) * 0.0005, this.box.position.y - 0.1 - (this.logo_height - 200) * 0.0005, -0.05);
        this.h_clone2.scaling.y = ((-0.49 * this.logo_height) - (1046.7 * this.box.position.y) + (1.008 * this.height) + 10.773) / 250;
        this.h_clone2.position = new BABYLON.Vector3(this.h_clone.position.x, this.h_clone.position.y + 0.2 + (this.logo_height - 200) * 0.001, -0.05);
        //文本参数
        this.logo_width_Text.position = new BABYLON.Vector3(this.box.position.x, this.box.position.y - 0.15 - (this.logo_height - 200) * 0.0005, -0.05);
        this.logo_height_Text.position = new BABYLON.Vector3(this.box.position.x + 0.19 + (this.logo_width - 200) * 0.0005, this.box.position.y, -0.05);
      }
      //LOGO顶部距离参数文本
      this.logo_distance_Text.dispose();
      var logo_distance_Text_string = ((-0.49 * this.logo_height) - (1046.7 * this.box.position.y) + (1.008 * this.height) + 10.773).toFixed(0).toString();
      var logo_distance_Text = BABYLON.MeshBuilder.CreateText("logo_distance_Text", logo_distance_Text_string + "mm", this.fontData, {
        size: 0.02,
        depth: 0.001
      });
      logo_distance_Text.material = this.logoMaterial;
      logo_distance_Text.position = new BABYLON.Vector3(this.box.position.x + 0.19, this.h_clone.position.y + 0.2 + (this.logo_height - 200) * 0.001 + (((-0.49 * this.logo_height) - (1046.7 * this.box.position.y) + (1.008 * this.height) + 10.773) * 0.0004), -0.05)
      logo_distance_Text.isVisible = false;
      this.logo_distance_Text = logo_distance_Text;
    });

    engine.runRenderLoop(function () {
      scene.render(); // 渲染场景
    });
    window.addEventListener("resize", function () {
      engine.resize();
    });
  },
  methods: {
    //添加LOGO
    handleFileUpload(event) {
      const file = event.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          const imageUrl = e.target.result;
          // 创建材质
          const material = new BABYLON.StandardMaterial('textureMaterial', this.scene);
          const texture = new BABYLON.Texture(imageUrl, this.scene);
          // 设置纹理的透明度处理方式
          texture.hasAlpha = true;
          material.diffuseTexture = texture;
          // 启用材质的透明度
          material.useAlphaFromDiffuseTexture = true;
          // 将材质应用到模型上
          this.box.material = material;
        };
        reader.readAsDataURL(file);
        this.logo_isVisible = true;
        this.box.isVisible = true;
        this.w_clone.isVisible = true;
        this.h_clone.isVisible = true;
        this.logo_width_Text.isVisible = true;
        this.logo_height_Text.isVisible = true;
        this.logo_distance_Text.isVisible = false;
        this.h_clone.isVisible = true;
        //this.h_clone2.isVisible = true;
      } else {
        this.logo_isVisible = false;
      }
    },
    screenshot() {
      this.scene.onReadyObservable.add(() => {
        BABYLON.Tools.CreateScreenshot(this.engine, this.camera, { precision: 1.0 });
      });
    }
  },
  watch: {
    product(newValue, oldValue) {
      //重置数值
      this.width = 300;
      this.height = 230;
      //删除旧模型
      this.bag.dispose();
      this.w.dispose();
      this.h.dispose();
      if (this.scene.meshes.find(mesh => mesh.id === "s")) {
        this.s.dispose();
      }
      if (this.scene.meshes.find(mesh => mesh.id === "bag2")) {
        this.bag2.dispose();
        this.bag2_clone.dispose();
        this.s_Text.dispose();
      }
      if (this.scene.meshes.find(mesh => mesh.id === "bag3")) {
        this.bag3.dispose();
      }
      //导入新模型
      BABYLON.SceneLoader.ImportMeshAsync("", "./", newValue + ".glb").then((result) => {
        this.bag = result.meshes.find(mesh => mesh.id === "bag")
        this.w = result.meshes.find(mesh => mesh.id === "w")
        this.w.material.emissiveColor = new BABYLON.Color3(1, 0, 0);
        this.h = result.meshes.find(mesh => mesh.id === "h")
        this.h.material.emissiveColor = new BABYLON.Color3(1, 0, 0);
        //是否有底部
        if (this.scene.meshes.find(mesh => mesh.id === "bag3")) {
          this.bag3 = result.meshes.find(mesh => mesh.id === "bag3")
          var w_Text_height = -0.21;
        } else {
          this.s_Text.dispose();
          var w_Text_height = -0.1;
        }
        //是否有侧面
        if (this.scene.meshes.find(mesh => mesh.id === "bag2")) {
          this.thickness_isVisible = true;
          this.thickness = 70;
          this.bag2 = result.meshes.find(mesh => mesh.id === "bag2")
          this.s = result.meshes.find(mesh => mesh.id === "s")
          this.s.material.emissiveColor = new BABYLON.Color3(1, 0, 0);
          //更新厚度数值
          var s_Text_string = this.thickness.toString();
          var s_Text = BABYLON.MeshBuilder.CreateText("s_Text", s_Text_string + "mm", this.fontData, {
            size: 0.02,
            depth: 0.001
          });
          s_Text.material = this.textMaterial;
          s_Text.position = s_Text.position = new BABYLON.Vector3((this.width - 300) * 0.001 + 0.22, w_Text_height, 0)
          this.s_Text = s_Text;
          //复制侧面
          this.bag2_clone = this.bag2.clone("bag2_clone");
          this.bag2_clone.position.x = -0.55;
        } else {
          this.thickness_isVisible = false;
        }
        //更新宽度数值
        this.w_Text.dispose();
        var w_Text_string = this.width.toString();
        var w_Text = BABYLON.MeshBuilder.CreateText("w_Text", w_Text_string + "mm", this.fontData, {
          size: 0.02,
          depth: 0.001
        });
        w_Text.material = this.textMaterial;
        w_Text.position = new BABYLON.Vector3((this.width - 300) * 0.0005, w_Text_height, 0)
        this.w_Text = w_Text;
        console.log(this.color.object)

      });
    },
    width(newValue, oldValue) {
      if (this.width > 50 && this.width < 1000) {
        //更新宽度
        this.bag.scaling.x = this.width / 300;
        this.w.scaling.x = this.width / 300;
        //右移
        this.bag.position.x = (this.width - 300) * -0.0005;
        this.w.position.x = (this.width - 300) * -0.0005;
        //是否有底部
        if (this.scene.meshes.find(mesh => mesh.id === "bag3")) {
          this.bag3.scaling.x = this.width / 300;
          this.bag3.position.x = (this.width - 300) * -0.0005;
          var w_Text_height = -0.21;
        } else {
          var w_Text_height = -0.1;
        }
        //是否有侧面
        if (this.scene.meshes.find(mesh => mesh.id === "bag2")) {
          //右移侧面
          //console.log(this.bag2.position.x)
          this.bag2.position.x = (this.width - 300) * -0.001 + ((this.thickness - 70) * 0.0025);
          this.bag2_clone.position.x = (this.width - 300) * -0.0028 - 0.55 + ((this.thickness - 70) * 0.0025);
          this.s.position.x = (this.width - 300) * -0.001 + ((this.thickness - 70) * 0.0025);

          //更新厚度显示数值
          this.s_Text.dispose();
          var s_Text_string = this.thickness.toString();
          var s_Text = BABYLON.MeshBuilder.CreateText("s_Text", s_Text_string + "mm", this.fontData, {
            size: 0.02,
            depth: 0.001
          });
          s_Text.material = this.textMaterial;
          s_Text.position = new BABYLON.Vector3((this.width - 300) * 0.001 + 0.22 + ((this.thickness - 70) * 0.0005), w_Text_height, 0)
          this.s_Text = s_Text;
        }
        //更新宽度显示数值
        this.w_Text.dispose();
        var w_Text_string = this.width.toString();
        var w_Text = BABYLON.MeshBuilder.CreateText("w_Text", w_Text_string + "mm", this.fontData, {
          size: 0.02,
          depth: 0.001
        });
        w_Text.material = this.textMaterial;
        w_Text.position = new BABYLON.Vector3((this.width - 300) * 0.0005, w_Text_height, 0)
        this.w_Text = w_Text;
      }
    },
    height(newValue, oldValue) {
      if (this.height > 50 && this.height < 1000) {
        //更新高度
        this.bag.scaling.y = this.height / 230;
        this.h.scaling.y = this.height / 230;

        //更新高度显示数值
        this.h_Text.dispose();
        var h_Text_string = this.height.toString();
        var h_Text = BABYLON.MeshBuilder.CreateText("h_Text", h_Text_string + "mm", this.fontData, {
          size: 0.02,
          depth: 0.001
        });
        h_Text.material = this.textMaterial;
        h_Text.position = new BABYLON.Vector3(-0.3, 0.13 + (this.height - 240) * 0.0005, 0)
        this.h_Text = h_Text;

        //是否有侧面
        if (this.scene.meshes.find(mesh => mesh.id === "bag2")) {
          this.bag2.scaling.y = this.height / 230;
          this.bag2_clone.scaling.y = this.height / 230;
        } else {
          this.thickness = 0;
        }

      }
    },

    thickness(newValue, oldValue) {
      if (this.thickness > 30 && this.thickness < 201) {
        //更新厚度
        this.bag2.scaling.x = this.thickness / 70;
        this.bag2_clone.scaling.x = this.thickness / 70;
        this.s.scaling.x = this.thickness / 70;
        //右移
        this.bag2.position.x = (this.width - 300) * -0.001 + ((this.thickness - 70) * 0.0025);
        this.bag2_clone.position.x = (this.width - 300) * -0.0028 - 0.55 + ((this.thickness - 70) * 0.0025);
        this.s.position.x = (this.width - 300) * -0.001 + ((this.thickness - 70) * 0.0025);
        //是否有底部
        if (this.scene.meshes.find(mesh => mesh.id === "bag3")) {
          var w_Text_height = -0.21;
        } else {
          var w_Text_height = -0.1;
        }
        //更新厚度显示数值
        this.s_Text.dispose();
        var s_Text_string = this.thickness.toString();
        var s_Text = BABYLON.MeshBuilder.CreateText("s_Text", s_Text_string + "mm", this.fontData, {
          size: 0.02,
          depth: 0.001
        });
        s_Text.material = this.textMaterial;
        s_Text.position = new BABYLON.Vector3((this.width - 300) * 0.001 + 0.22 + ((this.thickness - 70) * 0.0005), w_Text_height, 0)
        this.s_Text = s_Text;

      }
    },

    color(newValue, oldValue) {
      if (newValue.color.r == 0 && newValue.color.g == 0 && newValue.color.b == 0) {
      }
      else {
        const color_r = newValue.color.r / 255;
        const color_g = newValue.color.g / 255;
        const color_b = newValue.color.b / 255;
        if (!color_r == 0) {
          this.bag.material.emissiveColor = new BABYLON.Color3(color_r, color_g, color_b);
          this.bag.material.albedoColor = new BABYLON.Color3(color_r, color_g, color_b);
          //console.log(this.bag.material.albedoColor);
        }
        if (this.scene.meshes.find(mesh => mesh.id === "bag2")) {
          this.bag2.material.emissiveColor = new BABYLON.Color3(color_r, color_g, color_b);
          this.bag2.material.albedoColor = new BABYLON.Color3(color_r, color_g, color_b);
        }
        if (this.scene.meshes.find(mesh => mesh.id === "bag3")) {
          this.bag3.material.emissiveColor = new BABYLON.Color3(color_r, color_g, color_b);
          this.bag3.material.albedoColor = new BABYLON.Color3(color_r, color_g, color_b);
        }
        //更新RGB数值
        this.color_Text.dispose();
        var color_Text_string = color_r.toFixed(3) + ",  " + color_g.toFixed(3) + ",  " + color_b.toFixed(3);
        color_Text_string = color_Text_string.toString();
        var color_Text = BABYLON.MeshBuilder.CreateText("color_Text", "rgb: " + color_Text_string, this.fontData, {
          size: 0.02,
          depth: 0.001
        });
        color_Text.material = this.textMaterial;
        color_Text.position = new BABYLON.Vector3(0, -0.3, 0)
        this.color_Text = color_Text;
      }
    },
    logo_width(newValue, oldValue) {
      this.box.scaling.x = this.logo_width / 200;
      this.w_clone.scaling.x = 200 / 290 * this.logo_width / 200;
      this.h_clone.position = new BABYLON.Vector3(-this.box.position.x + 0.092 - (this.logo_width - 200) * 0.0005, this.box.position.y - 0.1, -0.05);
      this.logo_height_Text.position = new BABYLON.Vector3(this.box.position.x + 0.19 + (this.logo_width - 200) * 0.0005, this.box.position.y, -0.05)
      //LOGO宽度参数文本
      this.logo_width_Text.dispose();
      var logo_width_Text_string = this.logo_width.toString();
      var logo_width_Text = BABYLON.MeshBuilder.CreateText("logo_width_Text", logo_width_Text_string + "mm", this.fontData, {
        size: 0.02,
        depth: 0.001
      });
      logo_width_Text.material = this.logoMaterial;
      logo_width_Text.material.emissiveColor = new BABYLON.Color3(1, 0, 0);
      logo_width_Text.position = new BABYLON.Vector3(this.box.position.x, this.box.position.y - 0.15 - (this.logo_height - 200) * 0.0005, -0.05);
      this.logo_width_Text = logo_width_Text;

    },
    logo_height(newValue, oldValue) {
      this.box.scaling.y = this.logo_height / 200;
      this.box.position.y = this.box.position.y + (this.logo_height - 200) * 0.001
      this.h_clone.scaling.y = 200 / 235 * this.logo_height / 200;
      //LOGO高度参数文本
      this.logo_height_Text.dispose();
      var logo_height_Text_string = this.logo_height.toString();
      var logo_height_Text = BABYLON.MeshBuilder.CreateText("logo_height_Text", logo_height_Text_string + "mm", this.fontData, {
        size: 0.02,
        depth: 0.001
      });
      logo_height_Text.material = this.logoMaterial;
      logo_height_Text.material.emissiveColor = new BABYLON.Color3(1, 0, 0);
      logo_height_Text.position = new BABYLON.Vector3(this.box.position.x + 0.19 + (this.logo_width - 200) * 0.0005, this.box.position.y, -0.05);
      this.logo_height_Text = logo_height_Text;
    }
  },

};
</script>


